@import url('https://fonts.googleapis.com/css?family=Bungee');

body {
  background-color: #282c34;
}

p {
  color: white;
}

.doggo-image-container {
  border-color: white;
  border-style: solid;
  border-width: 2px;
  border-radius: 2px;
  width: 300px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

.doggo-image {
  object-fit: cover;
  width: 300px;
  height: 300px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0%);
}

video.doggo-image {
  z-index: 1;
}

img.doggo-image {
  z-index: 0;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.hidden {
  display: none;
}

.input-container {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 100%;
}

.input-button {
  cursor: pointer;
  color: white;
  font-size: 24px;
  background-color:#4CAF50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  margin: 10px;
  box-shadow: 0 7px #999;
  font-family: 'Bungee', cursive;
  text-decoration: none;
  outline: none;
}

.input-button:hover {
  background-color: #3E8E41;
}

.input-button:active {
  background-color: #3E8E41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

button[disabled=disabled], button[disabled] {
  background-color: grey;
}

button[disabled=disabled]:hover, button[disabled]:hover {
  background-color: grey;
}

button[disabled=disabled]:active, button[disabled]:active {
  box-shadow: 0 7px #999;
  transform: none;
}